.icons {
  list-style: none;
  padding: 0;
  margin: 0;
  li {
    display: inline-block;
    width: 50%;
    text-align: center;
    border: 1px solid #eee;
    border-radius: 4px;
    padding: 0;
    margin: 0 0 rem(36px) 0;
    box-sizing: border-box;
    @include bp-md {
      width: 25%;
    }
    p {
      width: 100%;
      text-align: center;
      line-height: 1;
      white-space: nowrap;
      background-color: #f7f7f7;
      padding: rem(4px);
      margin: 0 auto;
      &:first-of-type {
        border-bottom: 1px solid #eee;
      }
      &:last-of-type {
        border-top: 1px solid #eee;
      }
    }
    i {
      color: $brand-black;
      height: rem(56px);
      font-size: rem(32px);
      line-height: rem(56px);
    }
  }
}
