.btn {
  display: inline-block;
  cursor: pointer;
  color: $brand-white;
  text-align: center;
  padding: 5px 30px;
  border-radius: 0;
  box-shadow: none;
  background-color: $brand-primary;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 700;
  text-decoration: none;
  letter-spacing: 2px;
  line-height: 1.7em;
  border: 0;
  @include transition(all, .3s, ease);
  &:hover {
    background-color: $brand-secondary;
  }
}
