.breadcrumbs {
  list-style: none;
  padding: 0;
  margin: 0;
  li {
    display: inline-block;
    width: auto;
    padding-right: 18px;
    a {
      position: relative;
      &::after {
        content: "\203A";
        position: absolute;
        right: -12px;
      }
    }
    &:last-child {
      padding-right: 0;
      a:after {
        display: none;
      }
    }
  }
}
