$brand-white: #fff;
$brand-black: #222;
$brand-primary: #002a4f;
$brand-secondary: #6eb53d;
$brand-gray: #666;

$primary-font: "Open Sans", sans-serif;
$secondary-font: "Montserrat", sans-serif;

a {
  color: $brand-secondary;
  text-decoration: none;
  outline: none;
}

.pattern {
  color: $brand-gray;
  font-family: $primary-font;
  border-bottom: 2px solid lighten($brand-gray, 45%);
  padding-bottom: rem(24px);
  margin-bottom: rem(24px);
}

.pattern-header {

}

.pattern-title,
.pattern-title a {
  color: $brand-black;
  text-transform: capitalize;
  padding-top: rem(16px);
  margin-top: 0;
}

.page-content,
.pattern-content {
  line-height: 1.7em;
}
