// Global
*,
*:before,
*:after {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  &.menu-active {
    &::-webkit-scrollbar {
      display: none;
    }
  }
}

html {
  font-size: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  overflow-x: hidden;
}

body {
  position: relative;
  color: $black;
  font-family: $primary-font;
  font-size: rem(16px);
  line-height: 1;
  background: $white;
}

.wrapper {
  width: 100%;
  height: 100%;
}

.container {
  width: 85%;
  max-width: rem(1280px);
  overflow: hidden;
  margin: 0 auto;
  @include bp-sm { width: 90%; }
  @include bp-md { width: 92.5%; }
  @include bp-lg { width: 95%; }
  @include bp-xl { width: 97.5%; }
}

section {
  border-bottom: 1px solid #f5f2f0;
  padding-bottom: 1.83em;
}

.u-pad {
  padding: rem(32px);
  @include bp-md {
    padding: rem(64px) rem(32px) rem(64px) rem(32px);
  }
}

.-u-lft {
  @include bp-md {
    padding-right: 1em;
    p {
      padding-top: 0;
      margin-top: 0;
    }
  }
}

.-u-rgt {
  @include bp-md {
    padding-left: 1em;
    p {
      padding-top: 0;
      margin-top: 0;
    }
  }
}

// Header
.masthead {
  background-color: lighten($cool-grey, 65%);
  padding: 0 16px;
}

.header {
  position: relative;
  width:  100%;
  height: auto;
  overflow: hidden;
  @include transition( 0.35s, all, ease-in-out );
  @include bp-md {
    &.scrolled {
      position: fixed;
      max-width: 25%;
      .main-nav {
        li:not(.logo) {
          opacity: 0;
          visibility: hidden;
        }
      }
    }
  }
}

.nav-toggle {
  position: fixed;
  top: rem(36px);
  right: rem(16px);
  display: block;
  cursor: pointer;
  background-color: lighten($cool-grey, 65%);
  padding: 22px 24px 26px;
  border-radius: 4px;
  transform: translateY(-50%);
  z-index: 9999;
  @include bp-md {
    display: none;
  }
  span,
  span:before,
  span:after {
    content: "";
    cursor: pointer;
    border-radius: 1px;
    height: 6px;
    width: 36px;
    background: $cool-grey;
    position: absolute;
    left: 50%;
    display: block;
    transform: translateX(-50%);
    @include transition(0.35s, all, ease-in-out);
  }
  span:before {
    top: -12px;
  }
  span:after {
    bottom: -12px;
  }
}

.nav-toggle.menu-active {
  right: rem(32px);
  background-color: darken($cool-grey, 10%);
  span {
    background-color: transparent;
    transform: translateX(-50%);
  }
  span:before,
  span:after {
    top: 0;
  }
  span:before {
    background-color: $white;
    transform: translateX(-50%) rotate(45deg);
  }
  span:after {
    background-color: $white;
    transform: translateX(-50%) rotate(-45deg);
  }
}

// Nav
.main-nav {
  overflow: hidden;
  padding: 0;
  margin: 0;
  > li {
    float: left;
    display: none;
    line-height: 74px;
    padding: 0;
    margin: 0;
    @include transition( 0.35s, all, ease-in-out );
    > a {
      display: block;
      height: inherit;
      color: lighten($cool-grey, 25%);
      font-weight: bold;
      line-height: inherit;
      text-align: center;
      text-decoration: none;
      padding: 0 24px;
      @include transition( 0.2s, all, ease-in-out );
      &:focus,
      &:hover {
        background-color: lighten($cool-grey, 60%);
      }
    }
    &.logo {
      display: block;
      max-width: 112px;
      > a {
        padding: 16px 0;
        background-color: transparent !important;
      }
    }
    &.current {
      > a {
        position: relative;
        &:after {
          content: "";
          position: absolute;
          bottom: 0;
          left: 0;
          display: block;
          width: 100%;
          height: 6px;
          background-color: $cool-grey;
        }
      }
    }
    @include bp-md {
      display: block;
      &.logo {
        margin-right: 16px;
      }
    }
  }
}

.nav-sidebar {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  background-color: $cool-grey;
  opacity: 0;
  visibility: hidden;
  overflow-y: scroll;
  @include transition( 0.35s, opacity, ease-in-out );
  &.menu-active {
    opacity: 1;
    visibility: visible;
    z-index: 999;
  }
  @include bp-md {
    top: 74px;
    max-width: 25%;
    height: calc(100% - 74px);
    text-align: right;
    background-color: $white;
    opacity: 1;
    visibility: visible;
    transition: none;
  }
}

.sub-nav {
  padding: 4em 0 0;
  margin: 0;
  @include bp-md {
    padding-top: 1em;
  }
  > li {
    padding: 0;
    margin: .25em 0;
    > a {
      display: block;
      width: 100%;
      color: lighten($cool-grey, 25%);
      font-weight: bold;
      text-transform: capitalize;
      > span {
        display: inline-block;
        width: 100%;
        //border-radius: 4px;
        padding: .75em;
      }
      &:hover {
        > span {
          color: $white;
          background-color: darken($cool-grey, 10%);
          @include bp-md {
            color: lighten($cool-grey, 25%);
            background-color: lighten($cool-grey, 65%);
          }
        }
      }
      &.nav-active {
        > span {
          @include bp-md {
            background-color: lighten($cool-grey, 60%);
            border-left: 8px solid $cool-grey;
          }
        }
      }
    }
  }
}

// Main
main {
  width: 100%;
  padding: 16px 0;
  @include bp-md {
    width: 75%;
    margin-left: 25%;
  }
}

.page-title {
  margin-top: 8px;
}

.post-list {
  list-style: none;
  padding: 0;
  margin: 0;
  li {
    display: block;
    width: 100%;
  }
}

// Footer
.footer {
  padding: rem(32px) 0;
  font-size: rem(12px);
  line-height: 1.85;
  background-color: lighten($cool-grey, 65%);
  @include bp-md {
    margin-left: 25%;
  }
}
