.swatches {
  list-style: none;
  overflow: auto;
  > li {
    width: 100%;
    padding: 0 rem(4px);
    margin: rem(4px) auto;
    box-sizing: border-box;
    @include bp-md {
      float: left;
      width: 50%;
    }
    .swatch {
      text-align: center;
      border-radius: rem(4px);
      padding: rem(48px) 0;
      &.dark {
        color: $brand-white;
      }
      &.light {
        color: $brand-black;
      }
    }
  }
}
