.card {
  width: 100%;
  max-width: 300px;
  background-color: darken($brand-white, 5%);
  border-radius: 2px;
  border: 1px solid darken($brand-white, 10%);
}

.card-body {
  padding: 30px 20px;
}

.card-header {
  margin: 0 0 15px;
}

.card-title {
  font-size: 15px;
  font-family: $secondary-font;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-top: 0;
  a {
    display: block;
    color: $brand-black;
  }
}

.card-meta {
  font-family: $secondary-font;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 10px;
  margin: 0 0 5px;
}

.card-link {
  margin-top: 28px;
}
